import React from 'react';
import PasswordInput from 'react-password-indicator';
import SyntaxHighlighter from 'react-syntax-highlighter/prism';
// import logo from './logo.svg';
import Expansion from './Expansion';
import './App.css';

// Custom error messages
const errorMessages = {
  minLen: "Password is not long enough!",
  maxLen: val => `Password exceeded the maximum length of ${val}`,
  customAtRule: "Missing @! This message will be overridden.",
  mustMatch: "Passwords must match!",
  mainPasswordValid: "Password has to be valid!"
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { visible: false, value: "", validation: {} };
  }

  render() {
    return (
      <div className="Wrapper">
        <header className="Intro">
          <div>
            <h1>React Password Indicator</h1>
            <p>Flexible yet powerful password input with fully customizable render.</p>
            <p className="Links">
              <a href="https://github.com/Droow/react-password-indicator" rel="noopener noreferrer" target="_blank">View on GitHub</a>
              <a href="https://www.npmjs.com/package/react-password-indicator" rel="noopener noreferrer" target="_blank">NPM</a>
            </p>
          </div>
        </header>
        {/* Using controlled mode to toggle password visibility */}
        <div className="Row">
          <h2>Controlled mode</h2>
          Toggle button oustide of the input:<br />
          <button
            onClick={() =>
              this.setState(state => ({ visible: !state.visible }))
            }
          >
            Show password
          </button>
          {/* Using controlled mode show errors outside of the component */}
          {this.state.validation.errors && (
            <div>
              Erorrs outside of the input:<br />
              <ul>
                {this.state.validation.errors.map(e => (
                  <li key={e.key} className="error">{e.message}</li>
                ))}
              </ul>
            </div>
          )}
          <br />
          <PasswordInput
            minLen={4} // Optional predefined rule
            digits={2} // Optional predefined rule
            maxLen={10} // Optional predefined rule
            specialChars={2} // Optional predefined rule
            uppercaseChars={2} // Optional predefined rule
            defaultMessages={errorMessages}
            // Controlled mode
            // if not set the toggleShowPassword function in the render function should be used
            isVisible={this.state.visible}
            // Using controlled mode to handle state change
            onChange={(value, validation) =>
              this.setState({ value, validation })
            }
            // Additional custom rules
            rules={[
              {
                rule: val => val.indexOf("@") > -1,
                key: "customAtRule",
                // override the default message
                message: "Your password must contain the @ char."
              }
            ]}
          >
            {({
                getInputProps,
                getProgressProps,
                valid,
                isVisible,
                errors,
                touched
              }) => (
              <div>
                {/*
                Your input can be another component
                as long as it responds to onChange
                for example styled-component:
                */}
                <input
                  {...getInputProps()}
                  value={this.state.value}
                  className={touched ? (valid ? "valid" : "invalid") : ""}
                />
                {touched && (
                  <div>
                    <progress {...getProgressProps()} />
                    <p>Password is {valid ? "" : "in"}valid!</p>
                  </div>
                )}
              </div>
            )}
          </PasswordInput>
          <Expansion title={<h3>Code</h3>}>
            <SyntaxHighlighter
              language="javascript"
              showLineNumbers
            >
            {`
  constructor(props) {
    super(props);
    this.state = { visible: false, value: "", validation: {} };
  }

...

<button
  onClick={() =>
    this.setState(state => ({ visible: !state.visible }))
  }
>
  Show password
</button>
{/* Using controlled mode show errors outside of the component */}
{this.state.validation.errors && (
  <div>
    Erorrs outside of the input:<br />
    <ul>
      {this.state.validation.errors.map(e => (
        <li key={e.key}>{e.message}</li>
      ))}
    </ul>
  </div>
)}
<br />
<PasswordInput
  minLen={4} // Optional predefined rule
  digits={2} // Optional predefined rule
  maxLen={10} // Optional predefined rule
  specialChars={2} // Optional predefined rule
  uppercaseChars={2} // Optional predefined rule
  defaultMessages={errorMessages}
  // Controlled mode
  // if not set the toggleShowPassword function
  // in the render function should be used
  isVisible={this.state.visible}
  // Using controlled mode to handle state change
  onChange={(value, validation) =>
    this.setState({ value, validation })
  }
  // Additional custom rules
  rules={[
    {
      rule: val => val.indexOf("@") > -1,
      key: "customAtRule",
      // override the default message
      message: "Your password must contain the @ char."
    }
  ]}
>
  {({
      getInputProps,
      getProgressProps,
      valid,
      isVisible,
      errors,
      touched
    }) => (
    <div>
      {/*
      Your input can be another component
      as long as it responds to onChange
      for example styled-component:
      */}
      <input
        {...getInputProps()}
        value={this.state.value}
        className={touched ? (valid ? "valid" : "invalid") : ""}
      />
      <br />
      <br />
      {touched && (
        <div>
          <progress {...getProgressProps()} />
          <p>Password is {valid ? "" : "in"}valid!</p>
        </div>
      )}
    </div>
  )}
</PasswordInput>`}
            </SyntaxHighlighter>
          </Expansion>
        </div>
        <div className="Row">
          <h2>Uncontrolled mode</h2>
          Password confirmation
          <PasswordInput
            /* Disable predefined rules because the only
            rule we want is to match previous password */
            // minLen={4} // Optional predefined rule
            // digits={2} // Optional predefined rule
            // maxLen={10} // Optional predefined rule
            // specialChars={2} // Optional predefined rule
            // uppercaseChars={2} // Optional predefined rule
            mustMatch={this.state.value}
            defaultMessages={errorMessages}
            onChange={(val, state) =>
              console.log("Current pass", val, "and validation", state)
            }
            // Additional custom rules
            rules={[
              {
                rule: () => this.state.validation.valid,
                key: "mainPasswordValid",
              }
            ]}
          >
            {({
                getInputProps,
                getProgressProps,
                valid,
                isVisible,
                errors,
                toggleShowPassword,
                touched
              }) => (
              <div>
                <input
                  {...getInputProps()}
                  className={touched ? (valid ? "valid" : "invalid") : ""}
                />
                {/*
                  Uncontrolled mode
                  just using provided function and the visibility state is internal
                  WARNING: Doesn't work when isVisible prop is provided to the PasswordInput
                  */}
                <button onClick={toggleShowPassword}>
                  {isVisible ? "Hide" : "Show"}
                </button>
                <br />
                <br />
                {touched && (
                  <div>
                    {/*
                    We could display progress but it is
                    not needed for password confimration

                    <progress {...getProgressProps()} />
                    */}
                    <p>Confirmation password is {valid ? "" : "in"}valid!</p>
                    {errors && (
                      <ul>
                        {errors.map(e => <li key={e.key} className="error">{e.message}</li>)}
                      </ul>
                    )}
                  </div>
                )}
              </div>
            )}
          </PasswordInput>
          <Expansion title={<h3>Code</h3>}>
            <SyntaxHighlighter
              language="javascript"
              showLineNumbers
            >
            {`<PasswordInput
  /* Disable predefined rules because the only
  rule we want is to match previous password */
  // minLen={4} // Optional predefined rule
  // digits={2} // Optional predefined rule
  // maxLen={10} // Optional predefined rule
  // specialChars={2} // Optional predefined rule
  // uppercaseChars={2} // Optional predefined rule
  mustMatch={this.state.value}
  defaultMessages={errorMessages}
  onChange={(val, state) =>
    console.log("Current pass", val, "and validation", state)
  }
  // Additional custom rules
  rules={[
    {
      rule: () => this.state.validation.valid,
      key: "mainPasswordValid"
    }
  ]}
>
  {({
      getInputProps,
      getProgressProps,
      valid,
      isVisible,
      errors,
      toggleShowPassword,
      touched
    }) => (
    <div>
      <input
        {...getInputProps()}
        className={touched ? (valid ? "valid" : "invalid") : ""}
      />
      {/*
        Uncontrolled mode
        just using provided function and the visibility state is internal
        WARNING: Doesn't work when isVisible prop is provided
        to the PasswordInput
        */}
      <button onClick={toggleShowPassword}>
        {isVisible ? "Hide" : "Show"}
      </button>
      <br />
      <br />
      {touched && (
        <div>
          {/*
          We could display progress but it is
          not needed for password confimration

          <progress {...getProgressProps()} />
          */}
          <p>Confirmation password is {valid ? "" : "in"}valid!</p>
          {errors && (
            <ul>
              {errors.map(e => <li key={e.key}>{e.message}</li>)}
            </ul>
          )}
        </div>
      )}
    </div>
  )}
</PasswordInput>`}
            </SyntaxHighlighter>
          </Expansion>
        </div>
        <div className="Row">
          <h2>Always show rules</h2>
          <PasswordInput
            minLen={4} // Optional predefined rule
            digits={2} // Optional predefined rule
            maxLen={10} // Optional predefined rule
            specialChars={2} // Optional predefined rule
            uppercaseChars={2} // Optional predefined rule
          >
            {({
                getInputProps,
                getProgressProps,
                hasRulePassed,
                valid,
                isVisible,
                errors,
                rules,
                touched
              }) => (
              <div>
                {/*
                Your input can be another component
                as long as it responds to onChange
                for example styled-component:
                */}
                <input
                  {...getInputProps()}
                  className={touched ? (valid ? "valid" : "invalid") : ""}
                />
                {touched && <progress {...getProgressProps()} />}
                <ul>
                  {rules.map((r) => (
                    <li
                      key={r.key}
                      className={touched ? (hasRulePassed(r.key) ? 'passed' : 'error') : ''}
                    >
                      {r.message}
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </PasswordInput>
          <Expansion title={<h3>Code</h3>}>
            <SyntaxHighlighter
              language="javascript"
              showLineNumbers
            >
              {`<PasswordInput
  minLen={4} // Optional predefined rule
  digits={2} // Optional predefined rule
  maxLen={10} // Optional predefined rule
  specialChars={2} // Optional predefined rule
  uppercaseChars={2} // Optional predefined rule
>
  {({
      getInputProps,
      getProgressProps,
      hasRulePassed,
      valid,
      isVisible,
      errors,
      rules,
      touched
    }) => (
    <div>
      {/*
      Your input can be another component
      as long as it responds to onChange
      for example styled-component:
      */}
      <input
        {...getInputProps()}
        className={touched ? (valid ? "valid" : "invalid") : ""}
      />
      {touched && <progress {...getProgressProps()} />}
      <ul>
        {rules.map((r) => (
          <li
            key={r.key}
            className={touched ? (hasRulePassed(r.key) ? 'passed' : 'error') : ''}
          >
            {r.message}
          </li>
        ))}
      </ul>
    </div>
  )}
</PasswordInput>`}
            </SyntaxHighlighter>
          </Expansion>
        </div>
        <footer className="Footer">
          Created by {/*<a href="www.prdan.cz" rel="noopener noreferrer" target="_blank">Daniel Prala</a>*/} Daniel Prala
          <br />
          <br />
        </footer>
      </div>
    );
  }
}

export default App;



